.CenterBox {
  background-color: rgb(48,48,48,.0);
  opacity: 0.8;
  height: 90%;
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: 5.5em;
  font-weight: lighter;
}

.CenterBox * {
  color: white;
  text-align: center;
}

.CenterBox hr {
  width: 80%;
}

@media (max-width: 500px) {
  .CenterBox {
    width: 100%;
    opacity: 1;
  }
}







